.intro {
  background-color: $cBg;
  min-height: 100vh;
  &__wrapper {
    width: 100%;
    padding: 9rem 0 5rem;
    @media (min-width: 768px) {
      padding: 10rem 0; } }

  &__body {
    max-width: 30rem;
    width: 100%;
    @media (max-width: 649px) {
      max-width: 100%; }
    @media (min-width: 1024px) {
      max-width: 72rem; } }

  &__title {
    line-height: 1.15; }
  &__images {
    min-width: 26.4rem;
    align-self: flex-start;
    width: pct-s(400,1220);
    flex-shrink: 0;
    position: relative;
    margin-left: 3rem;
    margin-right: 1rem;
    @media (max-width: 649px) {
      display: none; }
    @media (min-width: 768px) {
      margin-right: -2rem; }
    @media (min-width: 1024px) {
      margin-right: 3rem; } }

  &__image {
    position: relative;
    &:before {
      padding-bottom: pct-s(503,400); } }

  &__decor {
    position: absolute;
    img {
      width: 100%;
      height: 100%; }
    &._one {
      top: 11%;
      left: -5%;
      width: pct-s(97,400); }
    &._two {
      top: 15%;
      right: 2%;
      width: pct-s(92,400); }
    &._three {
      top: 24%;
      right: -8%;
      width: pct-s(70,400); }
    &._four {
      top: 67%;
      right: 12%;
      width: pct-s(58,400); } } }

.form {

  &__row {
    margin: -1.5rem; }

  &__col {
    padding: 1.5rem;
    width: 100%;
    @media (min-width: 1024px) {
      width: 50%; } }

  &__btns {}

  &__policy {} }

.js-input-box {
  &._error {
    .item-form {
      &__input {
        border-color: $cRed; }
      &__error {
        opacity: 1;
        transform: none; }
      .checkbox__input {
        ~.checkbox {
          &__body {
            border-color: $cRed; } } } } } }
.item-form {
  position: relative;
  &__title {
    color: $cGray;
    font-size: 1.4rem;
    margin-bottom: 1rem;
    span {
      color: $cRed; } }

  &__label {
    display: block;
    width: 100%; }
  &__input {
    display: block;
    width: 100%;
    height: 5rem;
    padding: 0 1.5rem;
    font-size: 1.4rem;
    color: $cWhite;
    background-color: transparent;
    border: solid 1px rgba($cGray,.4);
    border-radius: .4rem;
    transition: border-color $trTime;
    &::placeholder {
      color: $cGray; }
    &:focus {
      border-color: $cGray; }
    @media (min-width: 768px) {
      height: 6rem;
      padding: 0 2.5rem; } }
  &__error {
    font-size: 1.2rem;
    color: $cRed;
    position: absolute;
    top: 100%;
    left: 0;
    margin-top: .5rem;
    opacity: 0;
    transform: translateY(-1rem);
    pointer-events: none;
    transition-property: transform, opacity;
    transition-duration: $trTime; } }

.checkbox {
  cursor: pointer;
  &__input {
    display: none;
    &:checked {
      ~.checkbox {
        &__body {
          background-color: $cRed;
          border-color: $cRed;
          svg {
            opacity: 1;
            transform: none; } } } } }
  &__body {
    flex-shrink: 0;
    width: 2.2rem;
    height: 2.2rem;
    border: 1px solid rgba(143, 149, 172, 0.3);
    border-radius: .2rem;
    margin-right: 1.2rem;
    transition-property: background-color, border-color;
    transition-duration: $trTime;
    @media (any-hover: hover) {
      &:hover {
        border-color: $cRed; } }
    @media (min-width: 768px) {
      margin-right: 1.7rem;
      width: 1.8rem;
      height: 1.8rem; }
    svg {
      opacity: 0;
      transform: scale(0);
      width: 1.3rem;
      height: 1.1rem;
      transition-property: opacity, transform;
      transition-duration: $trTime; } }

  &__text {
    font-size: 1.4rem;
    color: $cGray;
    max-width: 35rem;
    line-height: 1.3;
    a {
      color: $cRed;
      display: inline;
      @media (any-hover: hover) {
        &:hover {
          text-decoration: underline; } } } } }
